.main-user-menu {
  margin-bottom: 66px;
}

.main-user-menu__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.main-user-menu__search {
  position: relative;
}

.main-user-menu__search-input {
  width: 215px;
  padding-left: 10px;
  padding-top: 11px;
  padding-bottom: 10px;
  border: 1px solid #cccccc;
  border-radius: 2px;

  &::placeholder {
    font-size: 12px;
    .segoe-ui;
    opacity: 1;
  }

  &:focus {
    box-shadow: 0 1px 5px 0 fade(#7fcbce, 50%);
    border: 1px solid #59b7c2;
    outline: none;
  }
}

.main-user-menu__search-button {
  position: absolute;
  top: 1px;
  right: 0;
  width: 14px;
  height: 34px;
  margin-right: 8px;
  background-color: transparent;
  background-image: url("../img/icon-search.png");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}


@media (max-width: @desktop-width) {
  .main-user-menu__search {
    display: none;
  }
}

@media (max-width: @mobile-width-only) {
  .main-user-menu {
    display: none;
  }
}
