.promo {
  margin-bottom: 50px;
}

.promo-slider {
  position: relative;
  margin: 0;
  margin-bottom: 24px;
  padding: 0;
  list-style-type: none;
}

.promo-slider__item {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 480px;
  padding-top: 70px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-sizing: border-box;

  &--item-1 {
    background-color: #7abbc8;
    background-image: url("../img/slider-image-1.jpg");
  }

  &--item-2 {
    background-color: #9f9f9f;
    background-image: url("../img/slider-image-2.jpg");
  }

  &--item-3 {
    background-color: #ae9585;
    background-image: url("../img/slider-image-3.jpg");
  }

  &--item-2,
  &--item-3 {
    display: none;
  }
}

.promo-slider__desription {
  width: 490px;
  color: #222222;
}

.promo-slider__title {
  display: inline-block;
  margin: 0;
  margin-bottom: 18px;
  padding-top: 11px;
  padding-bottom: 13px;
  padding-left: 10px;
  padding-right: 7px;
  font-size: 36px;
  font-weight: 400;
  color: #222222;
  background-color: rgba(255, 255, 255, 0.7);
}

.promo-slider__text {
  margin: 0;
  font-size: 16px;
  line-height: 30px;
}

.promo-slider__text-line {
  display: inline-block;
  margin-bottom: 2px;
  padding-left: 11px;
  padding-right: 12px;
  background-color: rgba(255, 255, 255, 0.7);

  &:last-child {
    margin-bottom: 0;
  }
}

.promo-slider__switcher {
  position: absolute;
  top: 50%;
  margin-top: -21px;
  width: 42px;
  height: 42px;
  background-color: rgba(255, 255, 255, 0.85);
  border: none;
  cursor: pointer;

  &:hover {
    background-color: @dark;
  }

  &:hover::before,
  &:hover::after {
    background-color: @white;
  }

  &--prev {
    left: 0;
  }

  &--prev::before {
    content: "";
    position: absolute;
    top: 19px;
    left: 15px;
    width: 9px;
    height: 1px;
    background-color: @dark;
    transform: rotate(-45deg);
  }

  &--prev::after {
    content: "";
    position: absolute;
    bottom: 16px;
    left: 15px;
    width: 9px;
    height: 1px;
    background-color: @dark;
    transform: rotate(45deg);
  }

  &--next {
    right: 0;
  }

  &--next::before {
    content: "";
    position: absolute;
    top: 19px;
    left: 15px;
    width: 9px;
    height: 1px;
    background-color: @dark;
    transform: rotate(45deg);
  }

  &--next::after {
    content: "";
    position: absolute;
    bottom: 16px;
    left: 15px;
    width: 9px;
    height: 1px;
    background-color: @dark;
    transform: rotate(-45deg);
  }
}

.promo-slider__radio-switcher {
  position: absolute;
  bottom: 12px;
  right: 20px;
}

.promo-slider__radio-switcher-item {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 3px;
  border-radius: 50%;
  background-color: fade(@white, 60%);
  cursor: pointer;

  &:last-child {
    margin-right: 0;
  }

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 10px;
    height: 10px;
    margin-top: -5px;
    margin-left: -5px;
    border-radius: 50%;
    background-color: fade(#222222, 70%);
  }

  &:nth-child(n + 3)::after {
    margin-left: -6px;
  }

  &--active {
    background-color: fade(#222222, 75%);

    &::after {
      background-color: fade(@white, 75%);
    }
  }
}

@media (max-width: @desktop-width) {
  .promo-slider__item {
    justify-content: center;
  }

  .promo-slider__desription {
    width: 400px;
    text-align: center;
  }

  .promo-slider__radio-switcher {
    right: 50%;
    margin-right: -45px;
  }

  .promo-slider__radio-switcher-item {
    width: 26px;
    height: 26px;
  }
}

@media (max-width: @mobile-width-only) {
  .promo-slider__switcher {
    display: none;
  }

  .promo-slider__radio-switcher {
    margin-right: -52px;
  }

  .promo-slider__item {
    &--item-1 {
      background-image: url("../img/slider-image-1-mobile.jpg");
    }

    &--item-2 {
      background-image: url("../img/slider-image-2-mobile.jpg");
    }

    &--item-3 {
      background-image: url("../img/slider-image-3-mobile.jpg");
    }
  }

  .promo-slider__radio-switcher-item {
    width: 30px;
    height: 30px;
  }
}
