.features__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.features__item {
  width: 310px;
  border: 1px solid #e0e0e0;

  &:hover {
    border: 1px solid #cccccc;
  }
}

.features__link {
  display: block;
  padding-top: 23px;
  padding-bottom: 20px;
  padding-left: 109px;

  &--price {
    background-image: url("../img/icon-sale.png");
    background-repeat: no-repeat;
    background-position: 15px 12px;
  }

  &--new {
    background-image: url("../img/icon-new.png");
    background-repeat: no-repeat;
  }

  &--natural {
    background-image: url("../img/icon-natural.png");
    background-repeat: no-repeat;
    background-position: 15px 12px;
  }
}

.features__title {
  margin: 0;
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
  color: @dark;
}

.features__text {
  margin: 0;
  font-size: 20px;
  font-weight: 300;
  .segoe-ui;
  line-height: 28px;
  color: #777777;
}

@media (max-width: @desktop-width) {
  .features__item {
    width: 100%;
    margin-bottom: 20px;
    text-align: center;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .features__link {
    padding-left: 0;
  }

  .features__link--price {
    background-position-x: 200px;
  }

  .features__link--new {
    background-position-x: 184px;
  }

  .features__link--natural {
    background-position-x: 194px;
  }
}

@media (max-width: @mobile-width-only) {
  .features__link {
    background-image: none;
  }
}
