.currency-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.currency-list__item {
  margin-right: 1px;
  background-color: @grey;

  &--active {
    background-color: @light-blue;
  }
}

.currency-list__link {
  display: block;
  padding-top: 16px;
  padding-bottom: 4px;
  padding-left: 9px;
  padding-right: 7px;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 0.118em;
  color: @dark;

  &:hover:not(.active) {
    background: #d5e4f1;
  }
}

.active {
  background-color: @light-blue;
  color: @white;
}


.currency-list--adaptive {
  display: none;
}

@media (max-width: @mobile-width-only) {
  .currency-list--adaptive {
    display: block;
    margin: 0;
  }

  .currency-list__link {
    padding: 13px 0;
    width: 100%;
    text-align: center;
  }

  .main-nav--closed .currency-list__link {
    display: none;
  }

  .main-nav--opened .currency-list__link {
    display: block;
  }

  .main-nav--nojs .currency-list__link {
    display: block;
  }
}
