@font-face {
  font-family: "Bitter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Bitter-regular.woff2") format("woff2"), url("../fonts/Bitter-regular.woff") format("woff");
}

@font-face {
  font-family: "Bitter";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Bitter-bold.woff2") format("woff2"), url("../fonts/Bitter-bold.woff") format("woff");
}


@font-face {
  font-family: "Bitter";
  font-style: italic;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Bitter-italic.woff2") format("woff2"), url("../fonts/Bitter-italic.woff") format("woff");
}

@font-face {
  font-family: "Segoe UI";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/SegoeUI.woff") format("woff2"), url("../fonts/SegoeUI.woff2") format("woff");
}

@font-face {
  font-family: "Segoe UI";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/SegoeUI-light.woff2") format("woff2"), url("../fonts/SegoeUI-light.woff") format("woff");
}

@font-face {
  font-family: "Segoe UI";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/SegoeUI-bold.woff2") format("woff2"), url("../fonts/SegoeUI-bold.woff") format("woff");
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 24px;
  letter-spacing: 0.025em;
  font-family: "Bitter", "Arial", sans-serif;
  color: #444444;
  background: @white;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}
