.main-nav {
  position: relative;
  margin-bottom: 20px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #e0e0e0;
  }
}

.main-nav__adaptive {
  display: none;
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #f5f7f9;
}

.main_nav__item--active {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: -1px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #4ea7b1;
  }
}

.main_nav__item--submenu {
  position: relative;

  &:hover .submenu {
    display: block;
  }
}

.main_nav__link {
  display: block;
  padding-left: 18px;
  padding-right: 18px;
  padding-top: 15px;
  padding-bottom: 10px;
  font-size: 14px;
  font-family: "Bitter", "Arial", sans-serif;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: @dark;
  border: 1px solid transparent;

  &--active {
    background-color: #59b7c2;
    border: 1px solid #4ea7b1;
    color: @white;
  }

  &:hover:not(.main_nav__link--active) {
    border: 1px solid #c3d7e9;
    background-color: #d5e4f1;
  }
}

@media (max-width: @mobile-width-only) {
  .main_nav__link {
    display: block;
    padding-left: 0;
    padding-right: 0;
    text-align: center;
  }

  .main_nav__item {
    width: 100%;
    border-bottom: 1px solid #e0e0e0;

    &:last-child {
      border: none;
    }
  }

  .main-nav {
    margin-bottom: 0;
  }

  .main-nav__adaptive {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .main-nav--closed .main_nav__item {
    display: none;
  }

  .main-nav--opened .main_nav__item {
    display: block;
  }

  .main-nav--nojs .main_nav__item {
    display: block;
  }
}
