.submenu {
  display: none;
  flex-wrap: wrap;
  flex-direction: column;
  position: absolute;
  top: 50px;
  left: 0;
  width: 215px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: @white;
  border: 1px solid #e0e0e0;
  box-sizing: border-box;
  box-shadow: 0 2px 5px 0 fade(#848484, 35%);
  z-index: 100;
}

.submenu__item {
  border-bottom: 1px solid #e0e0e0;

  &:last-child {
    border: none;
  }
}

.submenu__link {
  display: block;
  padding-left: 18px;
  padding-top: 8px;
  padding-bottom: 7px;
  font-size: 13px;
  font-family: "Bitter", "Arial", sans-serif;
  color: #777777;

  &:hover {
    background-color: #f5f7f9;
  }
}

@media (max-width: @mobile-width-only) {
  .submenu {
    display: flex;
    position: inherit;
    top: auto;
    left: auto;
    width: 100%;
    background-color: inherit;
    border: none;
    box-shadow: none;
  }

  .submenu__link {
    padding-left: 0;
    text-align: center;

    &:hover {
      outline: 1px solid #c3d7e9;
      background-color: #d5e4f1;
    }
  }

  .submenu__item {
    border: none;
  }
}
