.modal-sign-up {
  position: fixed;
  top: 200px;
  left: 50%;
  width: 350px;
  margin-left: -175px;
  padding: 30px;
  border: 1px solid #e0e0e0;
  background-color: #ffffff;
  box-shadow: 0 2px 5px 0 rgba(132, 132, 132, 0.35);
  box-sizing: border-box;
  z-index: 100;
}

.modal-sign-up__label {
  font-size: 14px;
}

.modal-sign-up__input {
  width: 100%;
  margin-bottom: 10px;
  padding-left: 10px;
  padding-top: 11px;
  padding-bottom: 10px;
  border: 1px solid #cccccc;
  border-radius: 2px;

  &:focus {
    box-shadow: 0 1px 5px 0 fade(#7fcbce, 50%);
    border: 1px solid #59b7c2;
    outline: none;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.modal-sign-up__data {
  margin: 0;
  margin-bottom: 25px;

  &:last-child {
    margin-bottom: 30px;
  }
}

.modal-close-sign-up {
  position: absolute;
  top: 16px;
  right: 9px;
  width: 21px;
  height: 21px;
  font-size: 0;
  background: 0 0;
  border: 0;
  cursor: pointer;

  &::after,
  &::before {
    content: "";
    position: absolute;
    top: 9px;
    left: 0;
    width: 20px;
    height: 2px;
    background-color: @dark;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &::before {
    transform: rotate(45deg);
  }
}

.modal-sign-up__wrapper {
  display: flex;
  flex-direction: column;
}

.modal-sign-up__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
  color: @dark;
}

.modal-show {
  display: block;
  animation-name: bounce;
  animation-duration: 0.6s;
}

@media (max-width: @mobile-width-only) {
  .modal-sign-up {
    top: 50px;
    width: 300px;
    margin-left: -150px;
  }
}
