.page-header {
  font-size: 11px;
  .segoe-ui;
}

.page-header__toggle-menu {
  position: relative;
  display: none;
  width: 24px;
  height: 18px;
  margin-left: auto;
  background-color: @white;
  border: none;
  cursor: pointer;
}

.page-header__toggle-menu--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: @black;
  box-shadow: 0 8px 0 0 @black, 0 16px 0 0 @black;
}

.page-header__toggle-menu--opened {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: @black;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.page-header__toggle-menu--nojs {
  display: none;
}

@media (max-width: @mobile-width-only) {
  .page-header__toggle-menu {
    display: block;
  }
}
