.user-menu {
  position: relative;
  margin-bottom: 39px;
  background-color: #f5f7f9;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 9px;
    box-shadow: inset 0 3px 0 0 #59b7c2, inset 0 6px 0 0 #7fcbce, inset 0 9px 0 0 #aadce0;
  }
}

.user-menu__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.user-menu__phone {
  display: block;
  padding-top: 16px;
  padding-bottom: 4px;
  padding-left: 24px;
  color: @dark;
  background-image: url("../img/icon-phone.png");
  background-repeat: no-repeat;
  background-position: 0 21px;
  box-sizing: border-box;
}

.user-menu__account {
  margin-right: 100px;
}

.user-menu__text {
  padding-top: 16px;
  padding-bottom: 4px;
  margin: 0;
  font-size: 12px;
  font-family: "Bitter", "Arial", sans-serif;

  &--link {
    color: @blue;
  }

  &:hover {
    color: darken(@dark, 25%);
  }

  &--link:hover {
    color: darken(@blue, 10%);
  }
}

@media (max-width: @desktop-width) {
  .user-menu__account {
    margin-right: 0;
  }

  .lang-list {
    margin-right: 10px;
  }
}

@media (max-width: @mobile-width-only) {
  .user-menu {
    display: none;
  }
}
