.added-items {
  position: absolute;
  display: none;
  width: 310px;
  min-height: 100%;
  top: 36px;
  right: 0;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 18px;
  padding-bottom: 21px;
  border: 1px solid #e0e0e0;
  background-color: @white;
  box-shadow: 0 2px 5px 0 rgba(132, 132, 132, 0.35);
  box-sizing: border-box;
  z-index: 100;
}

.added-items__about {
  margin: 0;
  margin-bottom: 17px;
  font-size: 13px;
  color: #777777;
}

.added-items__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.added-items__item {
  position: relative;
  margin-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #f0f0f0;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    padding-bottom: 20px;
    border: none;
  }
}

.added-items__image {
  margin-right: 11px;
  width: 56px;
  height: 56px;
  border: 1px solid #e0e0e0;
}

.added-items__wrapper {
  display: flex;
  position: relative;
}

.added-items__title {
  margin: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
  .segoe-ui;
  line-height: 17px;
  color: #777777;
}

.added-items__sum {
  margin: 0;
  font-size: 12px;
  font-family: "Bitter", "Arial", sans-serif;
  line-height: 17px;
  color: @dark;
}

.added-items__remove-item {
  position: absolute;
  top: 3px;
  right: -7px;
  background: transparent;
  border: none;
  cursor: pointer;
}

.added-items__descr {
  width: 190px;
}

.added-items__actions {
  display: flex;
  flex-wrap: wrap;
}

.added-items__button {
  display: inline-block;
  margin-right: 10px;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 11px;
  .segoe-ui;
  border-radius: 2px;
  color: @white;

  &--cart {
    background-color: #59b7c2;
  }

  &--checkout {
    background-color: @dark;
  }

  &:last-child {
    margin-right: 0;
  }
}
