body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 24px;
  letter-spacing: 0.025em;
  font-family: "Bitter", "Arial", sans-serif;
  color: #444444;
  background: #ffffff;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.mobile-container {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}
.tablet-container {
  width: 708px;
  padding: 0 30px;
}
.desktop-container {
  width: 984px;
  padding: 0;
}
.segoe-ui {
  font-family: "Segoe UI", "Arial", sans-serif;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 984px;
  margin: 0 auto;
  padding: 0;
}
@media (max-width: 984px) {
  .container {
    width: 708px;
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 300px;
    margin: 0 auto;
    padding: 0 10px;
  }
}
.page-header {
  font-size: 11px;
  font-family: "Segoe UI", "Arial", sans-serif;
}
.page-header__toggle-menu {
  position: relative;
  display: none;
  width: 24px;
  height: 18px;
  margin-left: auto;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.page-header__toggle-menu--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.page-header__toggle-menu--opened::before,
.page-header__toggle-menu--opened::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.page-header__toggle-menu--opened::before {
  transform: rotate(45deg);
}
.page-header__toggle-menu--opened::after {
  transform: rotate(-45deg);
}
.page-header__toggle-menu--nojs {
  display: none;
}
@media (max-width: 767px) {
  .page-header__toggle-menu {
    display: block;
  }
}
.user-menu {
  position: relative;
  margin-bottom: 39px;
  background-color: #f5f7f9;
}
.user-menu::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 9px;
  box-shadow: inset 0 3px 0 0 #59b7c2, inset 0 6px 0 0 #7fcbce, inset 0 9px 0 0 #aadce0;
}
.user-menu__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.user-menu__phone {
  display: block;
  padding-top: 16px;
  padding-bottom: 4px;
  padding-left: 24px;
  color: #444444;
  background-image: url("../img/icon-phone.png");
  background-repeat: no-repeat;
  background-position: 0 21px;
  box-sizing: border-box;
}
.user-menu__account {
  margin-right: 100px;
}
.user-menu__text {
  padding-top: 16px;
  padding-bottom: 4px;
  margin: 0;
  font-size: 12px;
  font-family: "Bitter", "Arial", sans-serif;
}
.user-menu__text--link {
  color: #59b7c2;
}
.user-menu__text:hover {
  color: #040404;
}
.user-menu__text--link:hover {
  color: #3e9eaa;
}
@media (max-width: 984px) {
  .user-menu__account {
    margin-right: 0;
  }
  .lang-list {
    margin-right: 10px;
  }
}
@media (max-width: 767px) {
  .user-menu {
    display: none;
  }
}
.lang-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-right: 25px;
  padding: 0;
  list-style-type: none;
}
.lang-list__item {
  margin-right: 1px;
  background-color: #e6e9ed;
  text-transform: uppercase;
}
.lang-list__item:last-child {
  margin-right: 0;
}
.lang-list__link {
  display: block;
  padding-top: 16px;
  padding-bottom: 4px;
  padding-left: 10px;
  padding-right: 11px;
  font-weight: 700;
  color: #444444;
}
.lang-list__link:hover:not(.active) {
  background: #d5e4f1;
}
.active {
  background-color: #78c4cd;
  color: #ffffff;
}
.lang-list--adaptive {
  display: none;
}
@media (max-width: 767px) {
  .lang-list--adaptive {
    display: block;
    margin: 0;
  }
  .lang-list__link {
    padding: 13px 0;
    width: 100%;
    text-align: center;
  }
  .main-nav--closed .lang-list__link {
    display: none;
  }
  .main-nav--opened .lang-list__link {
    display: block;
  }
  .main-nav--nojs .lang-list__link {
    display: block;
  }
}
.currency-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.currency-list__item {
  margin-right: 1px;
  background-color: #e6e9ed;
}
.currency-list__item--active {
  background-color: #78c4cd;
}
.currency-list__link {
  display: block;
  padding-top: 16px;
  padding-bottom: 4px;
  padding-left: 9px;
  padding-right: 7px;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 0.118em;
  color: #444444;
}
.currency-list__link:hover:not(.active) {
  background: #d5e4f1;
}
.active {
  background-color: #78c4cd;
  color: #ffffff;
}
.currency-list--adaptive {
  display: none;
}
@media (max-width: 767px) {
  .currency-list--adaptive {
    display: block;
    margin: 0;
  }
  .currency-list__link {
    padding: 13px 0;
    width: 100%;
    text-align: center;
  }
  .main-nav--closed .currency-list__link {
    display: none;
  }
  .main-nav--opened .currency-list__link {
    display: block;
  }
  .main-nav--nojs .currency-list__link {
    display: block;
  }
}
.brand-logo {
  display: flex;
  flex-direction: column;
  margin-right: 38px;
}
.brand-logo__image {
  width: 132px;
  height: 56px;
  cursor: pointer;
}
.brand-logo__slogan {
  line-height: 4px;
  text-align: center;
  font-style: italic;
}
@media (max-width: 767px) {
  .brand-logo__image {
    width: 80px;
    height: 34px;
  }
}
.main-user-menu {
  margin-bottom: 66px;
}
.main-user-menu__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.main-user-menu__search {
  position: relative;
}
.main-user-menu__search-input {
  width: 215px;
  padding-left: 10px;
  padding-top: 11px;
  padding-bottom: 10px;
  border: 1px solid #cccccc;
  border-radius: 2px;
}
.main-user-menu__search-input::placeholder {
  font-size: 12px;
  font-family: "Segoe UI", "Arial", sans-serif;
  opacity: 1;
}
.main-user-menu__search-input:focus {
  box-shadow: 0 1px 5px 0 rgba(127, 203, 206, 0.5);
  border: 1px solid #59b7c2;
  outline: none;
}
.main-user-menu__search-button {
  position: absolute;
  top: 1px;
  right: 0;
  width: 14px;
  height: 34px;
  margin-right: 8px;
  background-color: transparent;
  background-image: url("../img/icon-search.png");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}
@media (max-width: 984px) {
  .main-user-menu__search {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-user-menu {
    display: none;
  }
}
.user-action-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.user-action-list__item {
  position: relative;
  margin-right: 3px;
  margin-left: 3px;
}
.user-action-list__item:last-child {
  margin-left: 0;
  margin-right: 0;
}
.user-action-list__item:first-child {
  margin-left: 0;
}
.user-action-list__item::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 14px;
  top: 12px;
  right: -3px;
  background-color: #e0e0e0;
}
.user-action-list__item:last-child::after {
  display: none;
}
.user-action-list__item:nth-last-child(2) {
  margin-right: 0;
}
.user-action-list__item:nth-last-child(2)::after {
  display: none;
}
.user-action-list__item--cart {
  position: relative;
}
.user-action-list__item--cart:hover .added-items {
  display: block;
}
.user-action-list__link {
  display: block;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 13px;
  padding-right: 11px;
  font-size: 12px;
  font-family: "Segoe UI", "Arial", sans-serif;
  color: #777777;
}
.user-action-list__link:hover {
  background-color: #59b7c2;
}
.user-action-list__link--cart {
  padding-left: 31px;
  padding-right: 9px;
  background-color: #f1f3f5;
  background-image: url("../img/icon-cart.png");
  background-repeat: no-repeat;
  background-position: 10px 12px;
}
.user-action-list__link--cart:hover {
  background-image: url("../img/icon-cart-white.png");
  color: #ffffff;
}
.user-action-list--adaptive {
  display: none;
}
@media (max-width: 767px) {
  .user-action-list--adaptive {
    display: block;
    margin: 0;
  }
  .user-action-list__link {
    padding: 13px 0;
    width: 100%;
    text-align: center;
  }
  .user-action-list__link:last-child {
    border: none;
  }
  .user-action-list__item {
    border-bottom: 1px solid #e0e0e0;
  }
  .user-action-list__link--cart {
    background-image: none;
  }
  .user-action-list__item::after {
    display: none;
  }
  .main-nav--closed .user-action-list__link {
    display: none;
  }
  .main-nav--opened .user-action-list__linkk {
    display: block;
  }
  .main-nav--nojs .user-action-list__link {
    display: block;
  }
}
.added-items {
  position: absolute;
  display: none;
  width: 310px;
  min-height: 100%;
  top: 36px;
  right: 0;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 18px;
  padding-bottom: 21px;
  border: 1px solid #e0e0e0;
  background-color: #ffffff;
  box-shadow: 0 2px 5px 0 rgba(132, 132, 132, 0.35);
  box-sizing: border-box;
  z-index: 100;
}
.added-items__about {
  margin: 0;
  margin-bottom: 17px;
  font-size: 13px;
  color: #777777;
}
.added-items__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.added-items__item {
  position: relative;
  margin-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #f0f0f0;
}
.added-items__item:first-child {
  margin-top: 0;
}
.added-items__item:last-child {
  padding-bottom: 20px;
  border: none;
}
.added-items__image {
  margin-right: 11px;
  width: 56px;
  height: 56px;
  border: 1px solid #e0e0e0;
}
.added-items__wrapper {
  display: flex;
  position: relative;
}
.added-items__title {
  margin: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
  font-family: "Segoe UI", "Arial", sans-serif;
  line-height: 17px;
  color: #777777;
}
.added-items__sum {
  margin: 0;
  font-size: 12px;
  font-family: "Bitter", "Arial", sans-serif;
  line-height: 17px;
  color: #444444;
}
.added-items__remove-item {
  position: absolute;
  top: 3px;
  right: -7px;
  background: transparent;
  border: none;
  cursor: pointer;
}
.added-items__descr {
  width: 190px;
}
.added-items__actions {
  display: flex;
  flex-wrap: wrap;
}
.added-items__button {
  display: inline-block;
  margin-right: 10px;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 11px;
  font-family: "Segoe UI", "Arial", sans-serif;
  border-radius: 2px;
  color: #ffffff;
}
.added-items__button--cart {
  background-color: #59b7c2;
}
.added-items__button--checkout {
  background-color: #444444;
}
.added-items__button:last-child {
  margin-right: 0;
}
.main-nav {
  position: relative;
  margin-bottom: 20px;
}
.main-nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e0e0e0;
}
.main-nav__adaptive {
  display: none;
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #f5f7f9;
}
.main_nav__item--active {
  position: relative;
}
.main_nav__item--active::before {
  content: "";
  position: absolute;
  top: -1px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #4ea7b1;
}
.main_nav__item--submenu {
  position: relative;
}
.main_nav__item--submenu:hover .submenu {
  display: block;
}
.main_nav__link {
  display: block;
  padding-left: 18px;
  padding-right: 18px;
  padding-top: 15px;
  padding-bottom: 10px;
  font-size: 14px;
  font-family: "Bitter", "Arial", sans-serif;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #444444;
  border: 1px solid transparent;
}
.main_nav__link--active {
  background-color: #59b7c2;
