.promo-slider__switcher {
  position: absolute;
  top: 50%;
  margin-top: -21px;
  width: 42px;
  height: 42px;
  background-color: rgba(255, 255, 255, 0.85);
  border: none;
  cursor: pointer;
}
.promo-slider__switcher:hover {
  background-color: #444444;
}
.promo-slider__switcher:hover::before,
.promo-slider__switcher:hover::after {
  background-color: #ffffff;
}
.promo-slider__switcher--prev {
  left: 0;
}
.promo-slider__switcher--prev::before {
  content: "";
  position: absolute;
  top: 19px;
  left: 15px;
  width: 9px;
  height: 1px;
  background-color: #444444;
  transform: rotate(-45deg);
}
.promo-slider__switcher--prev::after {
  content: "";
  position: absolute;
  bottom: 16px;
  left: 15px;
  width: 9px;
  height: 1px;
  background-color: #444444;
  transform: rotate(45deg);
}
.promo-slider__switcher--next {
  right: 0;
}
.promo-slider__switcher--next::before {
  content: "";
  position: absolute;
  top: 19px;
  left: 15px;
  width: 9px;
  height: 1px;
  background-color: #444444;
  transform: rotate(45deg);
}
.promo-slider__switcher--next::after {
  content: "";
  position: absolute;
  bottom: 16px;
  left: 15px;
  width: 9px;
  height: 1px;
  background-color: #444444;
  transform: rotate(-45deg);
}
.promo-slider__radio-switcher {
  position: absolute;
  bottom: 12px;
  right: 20px;
}
.promo-slider__radio-switcher-item {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 3px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
}
.promo-slider__radio-switcher-item:last-child {
  margin-right: 0;
}
.promo-slider__radio-switcher-item::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10px;
  height: 10px;
  margin-top: -5px;
  margin-left: -5px;
  border-radius: 50%;
  background-color: rgba(34, 34, 34, 0.7);
}
.promo-slider__radio-switcher-item:nth-child(n + 3)::after {
  margin-left: -6px;
}
.promo-slider__radio-switcher-item--active {
  background-color: rgba(34, 34, 34, 0.75);
}
.promo-slider__radio-switcher-item--active::after {
  background-color: rgba(255, 255, 255, 0.75);
}
