.user-action-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
}


.user-action-list__item {
  position: relative;
  margin-right: 3px;
  margin-left: 3px;

  &:last-child {
    margin-left: 0;
    margin-right: 0;
  }

  &:first-child {
    margin-left: 0;
  }

  &::after {
    content: "";
    position: absolute;
    width: 1px;
    height: 14px;
    top: 12px;
    right: -3px;
    background-color: #e0e0e0;
  }

  &:last-child::after {
    display: none;
  }

  &:nth-last-child(2) {
    margin-right: 0;
  }

  &:nth-last-child(2)::after {
    display: none;
  }

  &--cart {
    position: relative;

    &:hover .added-items {
      display: block;
    }
  }
}

.user-action-list__link {
  display: block;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 13px;
  padding-right: 11px;
  font-size: 12px;
  .segoe-ui;
  color: #777777;

  &:hover {
    background-color: #59b7c2;
  }

  &--cart {
    padding-left: 31px;
    padding-right: 9px;
    background-color: #f1f3f5;
    background-image: url("../img/icon-cart.png");
    background-repeat: no-repeat;
    background-position: 10px 12px;

    &:hover {
      background-image: url("../img/icon-cart-white.png");
      color: @white;
    }
  }
}

.user-action-list--adaptive {
  display: none;
}

@media (max-width: @mobile-width-only) {
  .user-action-list--adaptive {
    display: block;
    margin: 0;
  }

  .user-action-list__link {
    padding: 13px 0;
    width: 100%;
    text-align: center;

    &:last-child {
      border: none;
    }
  }

  .user-action-list__item {
    border-bottom: 1px solid #e0e0e0;
  }

  .user-action-list__link--cart {
    background-image: none;
  }

  .user-action-list__item::after {
    display: none;
  }

  .main-nav--closed .user-action-list__link {
    display: none;
  }

  .main-nav--opened .user-action-list__linkk {
    display: block;
  }

  .main-nav--nojs .user-action-list__link {
    display: block;
  }
}
