.brand-logo {
  display: flex;
  flex-direction: column;
  margin-right: 38px;
}

.brand-logo__image {
  width: 132px;
  height: 56px;
  cursor: pointer;
}

.brand-logo__slogan {
  line-height: 4px;
  text-align: center;
  font-style: italic;
}

@media (max-width: @mobile-width-only) {
  .brand-logo__image {
    width: 80px;
    height: 34px;
  }
}
