.lang-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-right: 25px;
  padding: 0;
  list-style-type: none;
}

.lang-list__item {
  margin-right: 1px;
  background-color: @grey;
  text-transform: uppercase;

  &:last-child {
    margin-right: 0;
  }
}

.lang-list__link {
  display: block;
  padding-top: 16px;
  padding-bottom: 4px;
  padding-left: 10px;
  padding-right: 11px;
  font-weight: 700;
  color: @dark;

  &:hover:not(.active) {
    background: #d5e4f1;
  }
}

.active {
  background-color: @light-blue;
  color: @white;
}

.lang-list--adaptive {
  display: none;
}

@media (max-width: @mobile-width-only) {
  .lang-list--adaptive {
    display: block;
    margin: 0;
  }

  .lang-list__link {
    padding: 13px 0;
    width: 100%;
    text-align: center;
  }

  .main-nav--closed .lang-list__link {
    display: none;
  }

  .main-nav--opened .lang-list__link {
    display: block;
  }

  .main-nav--nojs .lang-list__link {
    display: block;
  }
}
